import test from 'ava';

/*
 * Topic: Blocked-Scoped Declarations
 * Page: 7
 *************************************/

test('It should create correct scope', (t) => {
	t.plan(2);
	
	let a = 2;

	{
		let a = 3;
		this.is(a, 3);
	}

	t.is(a, 2);
});